.control{

    display: flex;

    .controlContainer{
        flex: 6;




        .widgetB{
            display: flex;
            justify-content: space-between;
            flex: 1;
            padding: 10px;
            -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
            box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
            border-radius: 10px;
            height: 100px;
        
            .left, .right{
                display: flex;
                flex-direction: column;
                justify-content: space-between;
        
            .title{
                font-weight: bold;
                font-size: 14px;
                color: rgb(162, 162, 162);
            }
        
            .counter{
                font-size:28px;
                font-weight:300;
            }
        
            .link{
                width:  max-content;
                font-size: 14px;
                border-bottom: 1px solid gray;
                cursor: pointer;
                color: rgb(83, 83, 82);
            }
        
            .percentage{
                display: flex;
                align-items: center;
                font-size: 18px;
                color: rgb(101, 6, 6);
            }
        
            .time{
                display: flex;
                align-items: center;
                font-size: 14px;
                color: rgb(56, 52, 52);
            }
        
        
            }
        
        }



        .widgetD{
